"use client"

import { ReactNode } from "react";

const CTAButton = ({active,text,className}:{active:boolean,text:string | ReactNode,className?:string}) => {
  return (
    <button className={`btn h-12 rounded-lg pt-3 pb-3 px-6 py-6 font-medium text-[16px] leading-6
    btn-xs sm:btn-sm md:btn-md lg:btn-lg xl:btn-xl
    ${active ? 'bg-[var(--yellow-50)] shadow-[-2px_-2px_0px_0px_#FFFFFF82_inset] text-[var(--richblack-900)]'
    :'bg-[var(--richblack-800)] shadow-[-2px_-2px_0px_0px_#FFFFFF2E_inset] text-[var(--richblack-5)]'} ${className}`}
    >
      {text}
    </button>
  )
}

export default CTAButton;