"use client"
import { ReactNode } from "react";
import CTAButton from "./CTAButton";
import { TypeAnimation } from "react-type-animation";

interface AnimatedCodeBlockProps {
    heading: ReactNode;
    description: string;
    active: boolean;
    btntext1: string | ReactNode;
    btntext2: string;
    toggleFlex: boolean;
    gradientToggle: boolean;
    codeBlock: string;
}
const AnimatedCodeBlock = ({heading,description,active,toggleFlex,btntext1,btntext2,gradientToggle,codeBlock}: AnimatedCodeBlockProps) => {
  return (
    <div className={`w-full h-[522px] mt-[30px] pt-[90px] pb-[90px] pl-[120px] pr-[120px] flex ${toggleFlex ? 'flex-row' : 'flex-row-reverse'}  items-center justify-between`}>
        <div className=" w-[486px] h-[284px] flex flex-col gap-3">
            <div className="w-full h-[88px] font-semibold text-4xl leading-11 tracking-[-2%]">
                {heading}
            </div>
            <p className="w-full h-[72px] text-base leading-[24px] text-[var(--richblack-300)] font-medium">
                {description}
            </p>
            <div className="flex gap-6 w-[338px] h-[100px] pt-[52px]">
                <CTAButton text={btntext1} active={active} className={`${toggleFlex ? 'w-[179px]' : 'w-[201px]'}`} />
                <CTAButton text={btntext2} active={!active} className="w-[135px]"/>
            </div>
        </div>
        <section className="w-[534px] h-[342px] p-8 flex flex-col gap-0.5">
            {/* <div className="bg-[linear-gradient(123.77deg,_#8A2BE2_-6.46%,_#FFA500_59.04%,_#F8F8FF_124.53%)] opacity-20 w-[373px] h-[257px] absolute top-0 left-0 z-[-1]">
            </div> */}
            <div className="w-[470px] h-[278px] border p-2 flex gap-2 bg-gradient-to-r relative from-[#0E1A2D3D] to-[#111E3261] border-solid
             before:absolute before:inset-0 before:p-[1px] before:rounded-lg before:bg-gradient-to-r before:from-[rgba(255,255,255,0.22)] before:to-[rgba(255,255,255,0)] before:-z-10">
                <div className="flex flex-col justify-center items-center w-6 h-full mx-auto font-bold text-[14px] leading-[22px] tracking-[-12%] text-[var(--richblack-400)]">
                    <p>1</p>
                    <p>2</p>
                    <p>3</p>
                    <p>4</p>
                    <p>5</p>
                    <p>6</p>
                    <p>7</p>
                    <p>8</p>
                    <p>9</p>
                    <p>10</p>
                    <p>11</p>
                </div>
                <div className="h-full w-[422px] text-[var(--brown-100)]">
                    <TypeAnimation
                    style={{ whiteSpace: 'pre-line', display: 'block' }}
                    sequence={[codeBlock,'',500]}
                    cursor={true}
                    repeat={Infinity}
                    speed={65}
                    omitDeletionAnimation={true}/>
                </div>
            </div>
        </section>
    </div>
  )
}

export default AnimatedCodeBlock;